import React from 'react';
import Row from './components/Row';
import TopBar from './components/TopBar';
import './App.css';

class item {
  constructor(value,key){
    this.value = value;
    this.key = key;
    this.css = "";
  }
  
}

class pairOfItems {
  constructor(first,second){
    this.first = first;
    this.second = second;
  }
  
}
    

var initialArray = []
for(var i = 0; i < 10; i++){
  initialArray.push(new item(i+1,i));
  
}

class App extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      inputType: 'numbers', 
      numberOfItems: '10',
      sortAlgorithmType: 'bubble sort', 
      items: initialArray, 
      index: 0,
      sorting: false
    }; //removed .keys() 
    //console.log("App Constructor");
  }

  callback_SADropdown = (SAType) => {
    this.setState({sortAlgorithmType: SAType});
  }

  callback_ITDropdown = (ITType) => {
    this.setState({inputType: ITType});
  }

  callback_NIDropdown = (NIType) => {
    this.setState({numberOfItems: NIType});
    var tempArr = Array(parseInt(NIType));
    for(var i = 0; i < parseInt(NIType); i++){
      tempArr[i] = new item(i+1,i);
    }
    this.setState({items: tempArr});
    //console.log(tempArr);
    //console.log(this.state.items);
    
  }

  swap = (outputArray, graduallySortedItems, i) => {
    var first,second, temp;
    
    //for(var i = 0; i < outputArray.length; i++) {
      first = outputArray[i].first;
      second = outputArray[i].second;
      temp = graduallySortedItems[first].value;
      graduallySortedItems[first].value = graduallySortedItems[second].value;
      graduallySortedItems[second].value = temp;
      this.setState({items: graduallySortedItems}); 
      this.setState({index: i+1});
      //console.log(i);
    //}

    
    /*
    this.swap
    setState
    iteratethrougharray
    wait
    */
  }


  bubbleSort = () => {
    //e.preventDefault();
    var outputArray = [];
    var swapp;
    var numberOfItems = parseInt(this.state.numberOfItems) - 1;
    
    var x = JSON.parse(JSON.stringify(this.state.items));    
    //console.log(x.map((item)=> {return item.value}));
    //console.log(graduallySortedItems.map((item)=> {return item.value}));
    do {
        swapp = false;
        for (var i=0; i < numberOfItems; i++) {
            if (x[i].value < x[i+1].value) {
              //console.log(x[i].key + " " + x[i+1].key);
              outputArray.push(new pairOfItems(x[i].key, x[i+1].key));
               var temp = x[i].value;
               x[i].value = x[i+1].value;
               x[i+1].value = temp;
               swapp = true;
              }
        }
        //numberOfItems--;
    } while (swapp);
    //console.log(x.map((item)=> {return item.value}));
    //console.log(outputArray.map((item)=> `${item.first}  ${item.second}`));
    //console.log(x.map((item)=> {return item.value}));
    return outputArray;
    //this.renderArray(outputArray);
    
  }

  quickSortSwap = (arr, i, j,outputArray) => {
    var temp = arr[i];
    arr[i] = arr[j];
    arr[j] = temp;
    if(arr[i].key != arr[j].key){
      outputArray.push(new pairOfItems(i, j));
    } // show every node touched 
  }

  partition = (arr, pivot, left, right, outputArray) => {
    var pivotValue = arr[pivot].value,
       partitionIndex = left;

   for(var i = left; i < right; i++){
    if(arr[i].value < pivotValue){
      this.quickSortSwap(arr, i, partitionIndex,outputArray);
      partitionIndex++;
    }
  }
  this.quickSortSwap(arr, right, partitionIndex,outputArray);
  return partitionIndex;
  }

  quickSort = (arr, left, right, outputArray) => {
    
    var len = arr.length, 
   pivot,
   partitionIndex;


  if(left < right){
    pivot = right;
    partitionIndex = this.partition(arr, pivot, left, right,outputArray);
    
   //sort left and right
   this.quickSort(arr, left, partitionIndex - 1,outputArray);
   this.quickSort(arr, partitionIndex + 1, right,outputArray);
  }
  return outputArray;
    
  }

  sort = (e) => {
    e.preventDefault();
    var outputArray = [];
    if(this.state.sortAlgorithmType == 'bubble sort'){
      outputArray = this.bubbleSort();
    }else if(this.state.sortAlgorithmType == 'quick sort') {
      outputArray = this.quickSort(JSON.parse(JSON.stringify(this.state.items)),
       0, this.state.items.length - 1, outputArray);
    }
    
    this.renderArray(outputArray)
  }

  changeItemRandomly = i => {
    if(this.state.sorting == true) return;
    this.setState(state => {
      const items = state.items.map((item, j) => {
        
        if (j === i) {          
          item.value = parseInt(Math.random()*this.state.numberOfItems + 1);
          return item;
        } else {
          return item;
        }
      });
      return {
        items,
      };
    });
    //console.log(i);
  };

  applyCSSToElements = (first,second, cssProperty) =>{
    var updatedItems = this.state.items.map((item)=> {
      if(item.key == first||item.key == second) {
        item.css = cssProperty;
        return item;
      }
      else return item;
    });
    this.setState({items: updatedItems});
  }

  addClass = (outputArray,x,classname) => {
    
    if(document.getElementById(outputArray[x].first).classList.contains(classname)){
      document.getElementById(outputArray[x].first).classList.remove(classname);
    } else document.getElementById(outputArray[x].first).classList.add(classname);
    if(document.getElementById(outputArray[x].second).classList.contains(classname)){
      document.getElementById(outputArray[x].second).classList.remove(classname);
    } else document.getElementById(outputArray[x].second).classList.add(classname);
  }

  pause = () => {
    this.setState({sorting:false});
  }

  renderArray = (outputArray) => {
    if(outputArray.length == 0) return;

    document.getElementById("sortBtn").classList.add("hidden"); 
    document.getElementById("pauseBtn").classList.remove("hidden"); 
    this.setState({sorting: true});


    var graduallySortedItems = this.state.items;
    var x = 0;
    var cash;
    
    var intervalID = setInterval(() => {
      
      if(x == cash){
        this.addClass(outputArray,x,"pizza");
      this.swap(outputArray, graduallySortedItems, x);
      document.getElementById(outputArray[x].first).classList.remove("pizza");

      if (++x === outputArray.length|| this.state.sorting === false) {
        window.clearInterval(intervalID);
        document.getElementById("pauseBtn").classList.add("hidden");
        document.getElementById("sortBtn").classList.remove("hidden")

      }
      } else {
        cash = x;
        this.addClass(outputArray,x,"pizza");
      }
        
      /*
      //document.getElementById(outputArray[x].first).classList.add("pizza");
      this.addClass(outputArray,x,"pizza");
      this.swap(outputArray, graduallySortedItems, x);
      //document.getElementById(outputArray[x].first).classList.remove("pizza");

      if (++x === outputArray.length) {
        window.clearInterval(intervalID);
      }*/
      if(outputArray.length === x) console.log("done rendering");
      //if(outputArray.length === x) document.getElementById("sortBtn").classList.remove("hidden");
      if(outputArray.length === x) this.setState({sorting: false});
    }, 150);
    
    
  }

  render() {
    //console.log("App Render called");
    //console.log(this.state.items);
    return (
      <div className="App">
        <TopBar SAcallBack = {this.callback_SADropdown} NIcallBack = 
        {this.callback_NIDropdown} ITcallBack = {this.callback_ITDropdown}/>
        <hr></hr>
        <Row items={this.state.items} CBchangeItem={this.changeItemRandomly}/>

        <p>ctrl+click or ctrl+hover to scramble</p>
        <button id="sortBtn" onClick={ this.sort }>Sort Items </button>
        <button id="pauseBtn" className="hidden" onClick={ this.pause }>Stop </button>
        <hr></hr>
        <div id="description">
          <h2>Description</h2>
            <p>This project gives a visual representation 
              of the inner workings of various sorting algorithms. 
              By displaying each swap, one is better able to see the effienciency
              (or inefficentcy) of an algorithm </p>
          <ul>
            <li>Project written in Javascript using React as a framework</li>
            <li>User interaction through Javascript mouse events</li>
            <li>CSS Animations and Transitions</li>
          </ul>
        </div>
        
      </div>
    );
  }
    
  
}

export default App;
